.searchBar {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 1rem;
    height: 30vh;
    width: 100%;
    color: white;
    background: center no-repeat url("../../assets/background/background.jpg");
}
.searchForm {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.1rem;
    margin: 0 auto 1rem;
    width: 50%;
    background: #FFFFFF;
}
.searchInput {
    height: 2rem;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
}
.page__title {
    text-align: center;
    margin-bottom: 1rem;
}
.page__title h1 {
    font-size: 3rem;
}
.page__title h4 {
    font-weight: 400;
}

.input {
    width: 100%;
    height: 100%;
    font-size: 1.5rem;
}

.btn {
    display: flex;
    justify-content: end;
    width: 2rem;
    padding: 0.1rem;
    background: center no-repeat url("../../assets/icons/search-icon.svg");
    border: none;
    height: 2rem;
    text-align: center;
    color: rgb(255, 255, 255);
    cursor: pointer;

}
.btn_content {
    display: none;
}
.sortForm {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.1rem;
    margin: 0 auto 1rem;
    width: 50%;
}
